import React, { PureComponent } from 'react';
import { Dropdown, Tabs, Badge, Avatar, Spin, List } from 'antd';
import { IconFont } from '@/components';
import classNames from 'classnames';
import EnvelopPNG from '@/assets/envelop.png';
import WarningPNG from '@/assets/warning.png';
import BellSVG from '@/assets/bell.svg';
import styles from './index.less';

const { TabPane } = Tabs;

export default class NoticeIcon extends PureComponent {
  static Tab = TabPane;

  // eslint-disable-next-line react/static-property-placement
  static defaultProps = {
    onPopupVisibleChange: () => {},
    onClear: () => {},
    loading: false,
  };

  getTriggerNode = (t) => {
    window.tNode = t;
    return t;
  };

  render() {
    const {
      className,
      count,
      onPopupVisibleChange,
      notices = [],
      onClear,
      fetchingNotices,
    } = this.props;
    const noticeButtonClass = classNames(className, styles.noticeButton);
    const dataSource = notices.map((v) => ({
      avatar:
        v.subject === '告警通知' || v.subject === '运维告警'
          ? WarningPNG
          : EnvelopPNG,
      title: v.subject,
      description: v.detail,
      key: v.notifyid,
    }));
    return (
      <Dropdown
        placement="bottomRight"
        overlay={
          <Spin spinning={fetchingNotices}>
            <div className={styles.noticeContent}>
              <div className={styles.noticeTitle}>
                <span className={styles.titleLeft}>通知</span>
                <span
                  className={styles.titleRight}
                  onClick={count !== 0 ? () => onClear(false) : null}
                >
                  全部标记为已读
                </span>
              </div>
              {notices.length ? (
                <List
                  className={styles.list}
                  dataSource={dataSource}
                  renderItem={(v) => (
                    <List.Item onClick={() => this.props.onItemClick(v.key)}>
                      <List.Item.Meta
                        avatar={<Avatar src={v.avatar} />}
                        title={v.title}
                        description={v.description}
                      />
                    </List.Item>
                  )}
                />
              ) : (
                <div className={styles.notFound}>
                  <div>
                    <img src={BellSVG} alt="not found" />
                    <p>没有未读通知</p>
                  </div>
                </div>
              )}
              <div
                className={styles.clear}
                onClick={() => onClear(true, !!notices.length)}
              >
                查看全部通知
              </div>
            </div>
          </Spin>
        }
        trigger={['click']}
        overlayClassName={styles.dropdown}
        getPopupContainer={this.getTriggerNode}
        onVisibleChange={onPopupVisibleChange}
      >
        <span className={noticeButtonClass}>
          <Badge
            count={count}
            className={count > 99 ? styles.plusBadge : styles.badge}
          >
            <IconFont
              type="icon-bell"
              style={{ fontSize: 16, color: 'rgba(255, 255, 255, 0.65)' }}
            />
          </Badge>
        </span>
      </Dropdown>
    );
  }
}
