import React, { PureComponent } from 'react';
import { Layout, Menu } from 'antd';
import pathToRegexp from 'path-to-regexp';
import { Link } from 'umi';
import { IconFont } from '@/components';
import styleb from '@/layouts/errBoundary.less';
import routesConfig from '../../../../config/router';
import styles from './index.less';

const { Sider } = Layout;
const { SubMenu } = Menu;
const siderFontSize = {
  fontSize: 12,
  marginTop: 0,
  paddingLeft: 0,
};

// Allow menu.js config icon as string or ReactNode
//   icon: 'setting',
//   icon: 'http://demo.com/icon.png',
//   icon: <Icon type="setting" />,
const getIcon = (icon) => {
  // console.log({ icon });
  if (typeof icon === 'string' && icon.indexOf('http') === 0) {
    return <img src={icon} alt="icon" className={styles.icon} />;
  }
  if (typeof icon === 'string') {
    return <IconFont type={icon} />;
  }
};
// BasicLayout Routes
const { routes: basicLayoutRoutes } = routesConfig[0].routes.find(
  (v) => v.path === '/',
);

export default class SiderMenu extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      openKeys: this.getOriginOpenKeys(),
      openKeysMemo: [],
    };
  }

  // eslint-disable-next-line react/no-deprecated
  componentWillReceiveProps(nextProps) {
    const nextPathArr = nextProps.location.pathname.split('/');
    const thisPathArr = this.props.location.pathname.split('/');
    if (nextPathArr[1] !== thisPathArr[1]) {
      // 同一顶级路由不改变
      this.setState({
        openKeys: this.getOriginOpenKeys(nextProps),
      });
    }
  }

  getOriginOpenKeys = (props) => {
    const {
      location: { pathname },
      menuData,
    } = props || this.props;
    const siderMenuData = this.getSiderMenuData(menuData, pathname) || [];
    // console.log(siderMenuData, 'siderMenuData');
    return siderMenuData.map((item) => item.path);
  };

  /**
   * Convert pathname to openKeys
   * /list/search/articles = > ['list','/list/search']
   * @param  props
   */
  // deprecated
  getDefaultCollapsedSubMenus(props) {
    const {
      location: { pathname },
    } = props || this.props;
    // eg. /list/search/articles = > ['','list','search','articles']
    let snippets = pathname.split('/');
    // Delete the end
    // eg.  delete 'articles'
    snippets.pop();
    // Delete the head
    // eg. delete ''
    snippets.shift();
    // eg. After the operation is completed, the array should be ['list','search']
    // eg. Forward the array as ['list','list/search']
    snippets = snippets.map((item, index) => {
      // If the array length > 1
      if (index > 0) {
        // eg. search => ['list','search'].join('/')
        return snippets.slice(0, index + 1).join('/');
      }
      // index 0 to not do anything
      return item;
    });
    snippets = snippets.map((item) => {
      return this.getSelectedMenuKeys(`/${item}`)[0];
    });
    // eg. ['list','list/search']
    return snippets;
  }

  /**
   * Recursively flatten the data
   * [{path:string},{path:string}] => {path,path2}
   * @param  menus
   */
  getFlatMenuKeys(menus) {
    let keys = [];
    menus.forEach((item) => {
      if (item.children) {
        keys.push(item.path);
        keys = keys.concat(this.getFlatMenuKeys(item.children));
      } else {
        keys.push(item.path);
      }
    });
    return keys;
  }

  getMenuName = (icon, name) => {
    return (
      <>
        {icon}
        {/* antd以anticon的兄弟span为判断name显隐的标准 */}
        <i className="anticon" style={{ display: 'none' }} />
        <span>{name}</span>
      </>
    );
  };

  /**
   * Get selected child nodes
   * /user/chen => /user/:id
   */

  getSelectedMenuKeys = (path) => {
    const { menuData } = this.props;
    const flatMenuKeys = this.getFlatMenuKeys(menuData);
    // console.log({ menuData });
    const { redirect, path: routePath } =
      basicLayoutRoutes.find((v) => v.redirect === path || v.path === path) ||
      {};
    return flatMenuKeys.filter((item) => {
      const isChildPath = routePath && routePath.includes(item);
      return (
        isChildPath ||
        pathToRegexp(
          redirect && redirect.includes(item) ? `${redirect}` : `/${item}`,
        ).test(path)
      );
    });
  };

  /**
   * 获取侧边菜单数据(1.26)
   */
  getSiderMenuData = (menuData, pathname) => {
    const selectedTopMenuKey = pathname.split('/')[1] || 'home';
    const siderMenuData = menuData.filter(
      (item) => item.path === selectedTopMenuKey,
    );
    return siderMenuData.length > 0 ? siderMenuData[0].children : [];
  };

  /**
   * 判断是否是http链接.返回 Link 或 a
   * Judge whether it is http link.return a or Link
   * @memberof SiderMenu
   */
  getMenuItemPath = (item) => {
    const flag = document.getElementsByClassName(styleb.errText1).length;
    const itemPath = this.conversionPath(item.path);
    const icon = getIcon(item.icon);
    const { target, name } = item;
    // Is it a http link
    if (/^https?:\/\//.test(itemPath)) {
      return (
        <a href={itemPath} target={target}>
          {this.getMenuName(icon, name)}
        </a>
      );
    }
    if (flag) {
      window.location.reload();
    }
    return (
      <Link
        to={itemPath}
        target={target}
        replace={itemPath === this.props.location.pathname}
        onClick={
          this.props.isMobile
            ? () => {
                this.props.onCollapse(true);
              }
            : undefined
        }
        title={name.length > 8 ? name : undefined}
      >
        {this.getMenuName(icon, name)}
        {/* <span>{name.length > 8 ? `${name.slice(0, 8)}...` : name}</span> */}
      </Link>
    );
  };

  // onTitleClick = () => {
  //   debugger;
  // };

  /**
   * get SubMenu or Item
   */
  getSubMenuOrItem = (item) => {
    if (item.children && item.children.some((child) => child.name)) {
      return (
        <SubMenu
          title={this.getMenuName(getIcon(item.icon), item.name)}
          // onTitleClick={this.onTitleClick}
          // title={
          //   item.icon ? (
          //     <span>
          //       {getIcon(item.icon)}
          //       <span>
          //         {item.name.length > 8
          //           ? `${item.name.slice(0, 8)}...`
          //           : item.name}
          //       </span>
          //     </span>
          //   ) : item.name.length > 8 ? (
          //     `${item.name.slice(0, 8)}...`
          //   ) : (
          //     item.name
          //   )
          // }
          key={item.path}
        >
          {this.getNavMenuItems(item.children)}
        </SubMenu>
      );
    } else {
      return (
        <Menu.Item style={siderFontSize} key={item.path}>
          {this.getMenuItemPath(item)}
        </Menu.Item>
      );
    }
  };

  /**
   * 获得菜单子节点
   * @memberof SiderMenu
   */
  getNavMenuItems = (menusData) => {
    if (!menusData) {
      return [];
    }
    return menusData
      .filter((item) => item.name && !item.path.includes('process'))
      .map((item) => {
        return this.getSubMenuOrItem(item);
      })
      .filter((item) => !!item);
  };

  // conversion Path
  // 转化路径
  conversionPath = (path) => {
    if (path && path.indexOf('http') === 0) {
      return path;
    } else {
      return `/${path || ''}`.replace(/\/+/g, '/');
    }
  };

  handleOpenChange = (newKeys) => {
    // const { menuData } = this.props;
    // const { openKeys } = this.state;
    // const lastOpenKey = newKeys[newKeys.length - 1];
    // const isMainMenu = menuData.some(
    //   (item) =>
    //     lastOpenKey && (item.key === lastOpenKey || item.path === lastOpenKey),
    // );
    this.setState({
      // openKeys: isMainMenu ? [lastOpenKey] : [...openKeys],
      openKeys: newKeys,
    });
  };

  handleCollapse = (flag, type) => {
    const { onCollapse, collapsed } = this.props;
    const { openKeys, openKeysMemo } = this.state;
    if (type === 'clickTrigger') {
      if (collapsed) {
        // 还原
        this.setState({
          openKeys: openKeysMemo,
        });
      } else {
        // 存储
        this.setState({
          openKeysMemo: openKeys,
        });
      }
      onCollapse(flag);
    }
  };

  render() {
    const {
      logo,
      logoText,
      collapsed,
      isMobile,
      location: { pathname },
      menuData,
    } = this.props;
    const { openKeys } = this.state;
    // Don't show popup menu when it is been collapsed
    const menuProps = collapsed
      ? {}
      : {
          openKeys,
        };
    // if pathname can't match, use the nearest parent's key
    let selectedKeys = this.getSelectedMenuKeys(pathname);
    if (!selectedKeys.length) {
      for (let i = 0; i < openKeys.length; i++) {
        if (!openKeys[i]) {
          openKeys.splice(i, 1);
          // eslint-disable-next-line
          i--;
        }
      }
      selectedKeys = [openKeys[openKeys.length - 1]];
    }
    // if the subSiderMenu shows
    const subSiderMenu = this.getNavMenuItems(
      this.getSiderMenuData(menuData, pathname),
    ).length;

    return subSiderMenu ? (
      <Sider
        trigger={
          <span className="trigger">
            <IconFont
              type="icon-forward"
              onClick={() => {}}
              style={{
                transform: !collapsed ? 'rotateY(-180deg)' : undefined,
              }}
            />
          </span>
        }
        collapsible
        collapsed={collapsed}
        breakpoint="md"
        onCollapse={this.handleCollapse}
        width={160}
        collapsedWidth={44}
        className={styles.sider}
      >
        {isMobile ? (
          <div className={styles.logo} key="logo">
            <Link to="/">
              <img src={logo} alt="logo" />
              <img src={logoText} alt="logo" />
            </Link>
          </div>
        ) : null}
        <Menu
          key="Menu"
          theme="dark"
          mode="inline"
          // inlineIndent={14}
          {...menuProps}
          onOpenChange={this.handleOpenChange}
          selectedKeys={selectedKeys}
          style={{
            width: '100%',
          }}
        >
          {this.getNavMenuItems(this.getSiderMenuData(menuData, pathname))}
        </Menu>
      </Sider>
    ) : null;
  }
}
