import React from 'react';
import { connect } from 'dva';
import PageHeaderLayout from '@/layouts/PageHeaderLayout';
import errorImg from '@/assets/errPage.png';
import styles from './authError.less';

interface AuthErrorProps {}

const AuthError = connect<{}, {}, {}, { user: any }>(({ user }) => ({
  user,
}))((props: AuthErrorProps) => {
  const {
    user: {
      authErrorType,
      // projectId,
      // currentUser: { projectInfo },
    },
  } = props;
  const getText1 = () => {
    switch (authErrorType) {
      case 1:
        return '您好像没有加入项目';
      case 2:
        return '您在此项目没有任何权限';
      case 3:
        return '该项目已被删除';
      default:
        return '';
    }
  };
  const getText2 = () => {
    switch (authErrorType) {
      case 1:
        return '请联系管理员';
      case 2:
        return '请联系项目管理员';
      case 3:
        return '请切换项目';
      default:
        return '';
    }
  };
  // console.log(projectInfo);
  const role = 'manager';
  // const cur = projectInfo.filter((item) => item.projectid === projectId);
  return (
    <PageHeaderLayout>
      <div className={styles.errBoundary}>
        <div>
          <div className={styles.errText1}>{getText1()}</div>
          <div className={styles.errText2}>{getText2(role)}</div>
          <img src={errorImg} alt="暂无图片" />
        </div>
      </div>
    </PageHeaderLayout>
  );
});

export default AuthError;
