/* eslint-disable no-shadow */
import React from 'react';
import PropTypes from 'prop-types';
import { Layout, notification, Spin } from 'antd';
import GlobalFooter from 'ant-design-pro/lib/GlobalFooter';
import DocumentTitle from 'react-document-title';
import { connect } from 'dva';
import { routerRedux } from 'dva/router';
import { withRouter, history } from 'umi';
import { ContainerQuery } from 'react-container-query';
import classNames from 'classnames';
import { fromJS } from 'immutable';
import {
  dsFavicon,
  LogoSvg,
  LogoText,
  LogoSvgl,
  platInfo,
  PreviewLoginPng,
} from '@/config/companyInfo';
import NotFound from '@/pages/Exception/404';
import AuthError from '@/pages/Exception/AuthError';
import {
  // formatter,
  // mergeMenuData,
  // formatReqData,
  // transformFrontData,
  // filterRoleMenu,
  computedRoutes,
  isPreview,
  RegulateURL,
} from '@/utils';
// import { originalMenuData } from '@/config/menu';
import styles from './index.less';
import ErrorBoundary from '../ErrorBoundary';
import GlobalHeader from './GlobalHeader';
import SiderMenu from './SiderMenu';
import { noFooterRoutes, noSiderRoutes, SCREENMAP } from './config';
import { isSiderRoutes } from './utils';
import routesConfig from '../../../config/router';
// import { findKeyOfMenu } from '@/pages/Platform/Pages';

const { Content } = Layout;

// const isDemoVersion = process.env.BUILD_MODE === 'preview';
const { routes: basicLayoutRoutes } = routesConfig[0].routes.find(
  (v) => v.path === '/',
);

function getNavList(array) {
  const navMap = array.map((item) => {
    return {
      key: item.navName,
      name: item.navName,
      path: item.path,
      role: item.roleId,
    };
  });
  return navMap;
}

function getAllRouteMap(arr) {
  const routeMap = {};
  getRouteMap(arr, routeMap);
  return routeMap;
}

function getRouteMap(arr, routeMap) {
  arr.forEach((item) => {
    if (item.path) {
      routeMap[item.path] = {
        name: item.name,
        auth: item.auth,
      };
    }
    if (item.children) {
      getRouteMap(item.children, routeMap);
    }
  });
}

class BasicLayout extends React.PureComponent {
  // eslint-disable-next-line react/static-property-placement
  static childContextTypes = {
    location: PropTypes.object,
    platformInfo: PropTypes.object,
    computedConfig: PropTypes.func,
    navList: PropTypes.array,
    filterVersionRoutesArr: PropTypes.array,
    allRouteDatas: PropTypes.object,
  };

  state = {
    initMenuLoading: false,
    previewPageLoading: false,
    menuDataArr: [],
  };

  getChildContext() {
    const {
      location,
      // routerDatas,
      platformInfo,
      reqAdminData,
      menuData,
      filterVersionRoutesArr,
    } = this.props;
    return {
      location,
      platformInfo,
      filterVersionRoutesArr,
      computedConfig: this.computedConfig,
      navList: getNavList(reqAdminData),
      allRouteDatas: getAllRouteMap(menuData),
    };
  }

  componentDidMount() {
    this.setFavicon();
    this.init();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const {
      location: { pathname },
      dispatch,
      user: { authRoutes, projectId: preProjectId },
    } = this.props;
    const {
      location: { pathname: nextPathname },
    } = nextProps;
    if (nextPathname && nextPathname !== pathname) {
      this.getCurrentPathAuth(authRoutes);
      this.checkIsShow(nextPathname);
      // 用户认证状态实时检测
      dispatch({
        type: 'user/getNotificationCount',
      }).then(({ res }) => {
        if (res && res.code === 409) {
          // 处理点击导航菜单时检测权限问题
          this.props
            .dispatch({
              type: 'user/getUserInfo',
            })
            .then(() => {
              this.computedConfig();
            });
        }
      });
      // 检测ds节点状态，实时刷新检测
      dispatch({
        type: 'user/getOverviewInfo',
      });
    }
    const curProjectId = localStorage.getItem('projectId') || '';
    // 处理多tab，切换项目的情况，若项目不同，获取最新信息
    if (curProjectId && preProjectId && preProjectId !== curProjectId) {
      this.handleChangeProject(curProjectId);
    }
  }

  componentWillUnmount() {
    if (this.timeId) clearInterval(this.timeId);
    if (this.checkUser) clearInterval(this.checkUser);
    this.resetMenu();
  }

  getCurrentPathAuth = (routes) => {
    const { hashPathname } = new RegulateURL();
    let currentPathAuth = '0';
    for (const key in routes) {
      if (hashPathname.slice(1).includes(key)) {
        currentPathAuth = routes[key].auth || '0';
      }
    }
    this.props.dispatch({
      type: 'user/updateState',
      payload: {
        currentPathAuth,
        canCurrentPageEdit: currentPathAuth !== '1',
      },
    });
  };

  setFavicon = () => {
    const { dispatch } = this.props;
    dispatch({ type: 'oem/getOemInfo', payload: {} }).then(() => {
      // 如果OEM功能为'true'，并且有图片地址，使用OEM配置里面的图片，否则使用默认图片
      const { oemList } = this.props.oem;
      const { faviconfile, product, isoem, ip } = oemList;
      const link =
        document.querySelector("link[rel*='icon']") ||
        document.createElement('link');
      link.type = 'image/x-icon' || 'image/vnd.microsoft.icon';
      link.rel = 'shortcut icon';
      link.href =
        isoem === 'true' && ip && faviconfile
          ? `//${ip}/${faviconfile}`
          : dsFavicon;
      document.getElementsByTagName('head')[0].appendChild(link);
      localStorage.setItem(
        'product',
        isoem === 'true' && product ? product : platInfo,
      );
      localStorage.setItem('oemInfo', JSON.stringify(oemList));
    });
  };

  // eslint-disable-next-line react/sort-comp
  init = () => {
    const fiveMins = 5 * 60 * 1000;
    // 自动刷新token （页面管理中的预览页不做此处理）
    const { hashParams } = new RegulateURL();
    const isPreviewPage = hashParams.hasOwnProperty('token');
    if (!isPreviewPage) {
      this.timeId = setInterval(() => {
        this.props.dispatch({
          type: 'global/refreshToken',
        });
      }, fiveMins);
    }
    this.setState(
      {
        initMenuLoading: true,
      },
      () => {
        this.props
          .dispatch({
            type: 'user/getUserInfo',
          })
          .then(() => {
            // 处理登录多用户退出问题 （页面管理中预览页不退出）
            if (!isPreviewPage) {
              this.checkUser = setInterval(() => {
                const { currentUser, dispatch } = this.props;
                const curUser = localStorage.getItem('userid');
                const preUser = currentUser.userid;
                if (curUser && preUser && preUser !== curUser) {
                  localStorage.setItem('anotherUserId', curUser);
                  dispatch({
                    type: 'login/loginOutWithToken',
                  });
                }
              }, 20000);
            }
            this.computedConfig();
          });

        this.props.dispatch({
          type: 'user/getNotificationCount',
        });
        this.props.dispatch({
          type: 'user/getOverviewInfo',
        });
        this.props.dispatch({
          type: 'global/getVersion',
        });
      },
    );
  };

  resetMenu = () => {
    this.props.dispatch({
      type: 'user/updateState',
      payload: {
        currentMenuData: [],
      },
    });
  };

  filterMenuArr = (data = this.props.menuData) => {
    const menuRoutesObj = {};
    const menuDataArr = [];
    this.traverseTree(data, (item) => {
      const { address, pageUrl, isOriginal } = item;
      Reflect.defineProperty(menuRoutesObj, `${address}`, {
        value: {
          pageUrl,
          isOriginal,
        },
        enumerable: true,
        configurable: true,
        writable: true,
      });
      menuDataArr.push({
        address,
      });
    });
    // process的隐藏状态获取第一层
    data.forEach((item) => {
      const { children, path } = item;
      Reflect.defineProperty(menuRoutesObj, `${path}`, {
        value: {
          pageUrl: item.pageUrl,
          isOriginal: item.isOriginal,
        },
        enumerable: true,
        configurable: true,
        writable: true,
      });
      if (path === 'process') {
        children.forEach((child) => {
          Reflect.defineProperty(menuRoutesObj, `${child.path}`, {
            value: {
              pageUrl: child.pageUrl,
              isOriginal: child.isOriginal,
            },
            enumerable: true,
            configurable: true,
            writable: true,
          });
        });
        menuDataArr.push({
          address: `/${children[0] ? children[0].path : ''}`,
        });
      }
    });
    const isPreviewVersion = process.env.BUILD_MODE === 'preview';
    const newRouterArr = fromJS(basicLayoutRoutes).toJS();
    const filterVersionRoutesArr = newRouterArr.filter(
      ({ isPreview, component }) =>
        component !== './Custom' &&
        (isPreviewVersion
          ? isPreview === undefined || isPreview === true
          : isPreview === undefined || isPreview === false),
    );
    Object.keys(menuRoutesObj).forEach((key) => {
      const { isOriginal } = menuRoutesObj[key];
      // 添加新增页面的路由信息
      if (isOriginal === false) {
        filterVersionRoutesArr.push({
          ...menuRoutesObj[key],
          path: key,
        });
      }
    });
    this.props
      .dispatch({
        type: 'user/setFilterVersionRoutesArr',
        payload: {
          filterVersionRoutesArr,
        },
      })
      .then(() => {
        this.setState(
          {
            menuDataArr,
          },
          () => {
            this.checkIsShow();
          },
        );
      });
  };

  // 导航栏路由重定向
  checkIsShow = (pathname = this.props.location.pathname) => {
    const {
      location,
      user: { menuRoutesArr },
    } = this.props;
    const { search } = location;
    const { menuDataArr } = this.state;

    const isPreviewPage = search.includes('isHidden=true');
    if (!isPreviewPage) {
      // 原先用于 path'/' 和新建页面的重定向
      const redirectItem = menuRoutesArr.find(
        (v) => !v.isOriginal && v.address === pathname,
      );
      const currentItem = menuRoutesArr.find((v) => v.address === pathname);

      if (redirectItem || !currentItem) {
        // 进入页面是否存在重定向
        const newRedirect = menuDataArr.find((v) => {
          return v.address.includes(pathname);
        });

        if (newRedirect && newRedirect.address !== pathname) {
          history.replace({
            pathname: newRedirect.address,
          });
        }
      }
    }
  };

  traverseTree = (tree, cb) => {
    tree.forEach((value) => {
      const { children } = value;
      if (children) {
        this.traverseTree(children, cb);
      } else {
        cb(value);
      }
    });
  };

  computedConfig = () => {
    const { search } = this.props.location;
    const isPreviewPage = search.includes('isPreview=true');

    this.props
      .dispatch({
        type: 'user/getMenuList',
        payload: {
          isrelease: !isPreviewPage,
        },
      })
      .then(({ menuData }) => {
        if (isPreviewPage) {
          this.setState({
            previewPageLoading: true,
          });
        }
        this.dataHandling(menuData);
      });
  };

  dataHandling = (data) => {
    const routerDatas = {};
    basicLayoutRoutes.forEach((item) => {
      const { path, ...others } = item;
      Reflect.defineProperty(routerDatas, `${path}`, {
        value: others,
        enumerable: true,
        configurable: true,
        writable: true,
      });
    });
    const newRouterDatas = computedRoutes(routerDatas, data);
    const authRoutes = getAllRouteMap(data);
    this.getCurrentPathAuth(authRoutes);
    this.props.dispatch({
      type: 'user/updateState',
      payload: {
        authRoutes,
      },
    });
    this.props
      .dispatch({
        type: 'user/setCurrentMenuData',
        payload: {
          routerDatas: newRouterDatas,
        },
      })
      .then(() => {
        this.filterMenuArr(data);
        this.setState({
          initMenuLoading: false,
          previewPageLoading: false,
        });
      });
  };

  updateState = (payload) => {
    this.props.dispatch({
      type: 'user/updateState',
      payload,
    });
  };

  handleMenuCollapse = (collapsed) => {
    this.props.dispatch({
      type: 'global/changeLayoutCollapsed',
      payload: collapsed,
    });
  };

  logout = () => {
    this.props
      .dispatch({
        type: 'user/logout',
        payload: {
          logout: true,
        },
      })
      .then(() => {
        // 用户主动登出，设置字段user.logout为true(组件EditTabs需要)
        this.props.dispatch({ type: 'login/logoutInfo' });
        this.props.dispatch({ type: 'login/logout' });
      });
  };

  handleMenuClick = ({ key }) => {
    switch (key) {
      case 'profile':
        this.props.dispatch(routerRedux.push('/personal/account/profile'));
        break;
      case 'log':
        this.props.dispatch(routerRedux.push('/personal/account/log'));
        break;
      case '403':
        this.props.dispatch(routerRedux.push('/exception/403'));
        break;
      case 'logout':
        this.logout();
        break;
      default:
        break;
    }
  };

  handleNoticeVisibleChange = (visible) => {
    if (visible) {
      this.props.dispatch({
        type: 'global/fetchNotices',
        payload: { perpage: 100 },
      });
    }
  };

  // 清空消息弹框的所有通知，或者查看全部通知
  handleNoticeClear = (isback = false, isLength = false) => {
    const { dispatch, location } = this.props;
    if (location.pathname === '/personal/info/notification') {
      dispatch({ type: 'global/clearNotices' }).then(() => {
        notification.success({ message: '通知已清空！' });
      });
      return;
    }
    if (isback) {
      dispatch(
        routerRedux.push({
          pathname: '/personal/info/notification',
          state: isLength ? 'notRead' : 'read',
        }),
      );
    } else {
      dispatch({ type: 'global/clearNotices' }).then(() => {
        notification.success({ message: '通知已清空！' });
      });
    }
  };

  handleItemClick = (notifyid) => {
    event.stopPropagation();
    const { dispatch } = this.props;
    if (location.pathname === '/personal/info/notification') {
      notification.warning({ message: '您已在消息通知页面！' });
    } else {
      dispatch(
        routerRedux.push({
          pathname: '/personal/info/notification',
          state: 'read',
        }),
      );
    }
    dispatch({
      type: 'psNotification/markRead',
      payload: { notifyid },
    });
  };

  getPageTitle() {
    const { routerDatas, location } = this.props;
    const { pathname } = location;
    const product = localStorage.getItem('product');
    let title = product;
    if (routerDatas[pathname] && routerDatas[pathname].name) {
      title = `${routerDatas[pathname].name} - ${product}`;
    }
    return title;
  }

  handleChangeProject = (projectId) => {
    const {
      user: { menuData },
    } = this.props;
    const { menuDataArr } = this.state;
    this.props
      .dispatch({
        type: 'user/changeProject',
        payload: {
          projectid: projectId,
        },
      })
      .then(({ code }) => {
        if (code === 200) {
          this.computedConfig();
          this.props.dispatch({
            type: 'user/getUserInfo',
          });
          if (menuData.length > 0) {
            const firstItemPath = menuData[0].path;
            const currentItem = menuDataArr.find((v) =>
              v.address.includes(firstItemPath),
            );
            this.props.dispatch(
              routerRedux.push({
                pathname: `${currentItem.address}`,
              }),
            );
            location.reload();
          }
        }
      });
  };

  renderBasicLayout = () => {
    const {
      user: { authErrorType, menuRoutesArr },
      location: { pathname, search },
      location,
      currentUser,
      notifyCount,
      fetchingNotices,
      notices,
      collapsed,
      menuData,
    } = this.props;
    const {
      // isMobile,
      initMenuLoading,
    } = this.state;
    const orgLoginPic = isPreview ? PreviewLoginPng : LogoSvgl;

    // 以下路由不在后台返回数据里
    const alwaysCanViewPage = [
      '/personal/account/profile',
      '/personal/account/log',
      '/personal/info/notification',
    ];

    const isPreviewPage = search.includes('isPreview=true');
    let isShow = true;

    if (isPreviewPage) {
      const isRedirect =
        (authErrorType === 0 && menuRoutesArr.length === 0) ||
        this.state.previewPageLoading;
      isShow =
        Boolean(menuRoutesArr.find((v) => v.address.includes(pathname))) ||
        pathname === '/' ||
        alwaysCanViewPage.includes(pathname) ||
        isRedirect;
    } else {
      isShow =
        Boolean(menuRoutesArr.find((v) => pathname.includes(v.address))) ||
        pathname === '/' ||
        alwaysCanViewPage.includes(pathname);
    }

    const showPageCondition =
      authErrorType === 0 || alwaysCanViewPage.includes(pathname);

    return (
      <Layout>
        <GlobalHeader
          logo={orgLoginPic}
          currentUser={currentUser}
          notifyCount={notifyCount}
          fetchingNotices={!!fetchingNotices}
          notices={notices}
          menuData={menuData}
          collapsed={collapsed}
          location={location}
          isMobile={this.state.isMobile}
          onNoticeClear={this.handleNoticeClear}
          onCollapse={this.handleMenuCollapse}
          onMenuClick={this.handleMenuClick}
          onNoticeVisibleChange={this.handleNoticeVisibleChange}
          onRedirect={this.handleItemClick}
          handleChangeProject={this.handleChangeProject}
        />
        <Layout>
          {!noSiderRoutes.includes(pathname) && menuData.length ? (
            <SiderMenu
              logo={LogoSvg}
              logoText={LogoText}
              menuData={menuData}
              collapsed={collapsed}
              location={location}
              // isMobile={isMobile}
              onCollapse={this.handleMenuCollapse}
            />
          ) : // null
          null}
          <Layout
            className={styles.layoutStyle}
            style={{
              // 模拟数据地图页面滚动条透明色
              backgroundColor: pathname === '/home' ? '#031b36' : '#f0f2f5',
            }}
          >
            <div
              className={`${styles.layoutContent} ${
                pathname === '/home' ? styles.homeLayoutStyle : ''
              }`}
              style={{
                left: isSiderRoutes(noSiderRoutes, pathname) ? '160px' : 0,
              }}
            >
              <div style={{ height: `calc(100vh - 48px)`, overflow: 'hidden' }}>
                <Content id="basicLayout">
                  <ErrorBoundary>
                    {!initMenuLoading &&
                      (isShow ? (
                        showPageCondition ? (
                          this.props.children
                        ) : (
                          <AuthError />
                        )
                      ) : (
                        NotFound
                      ))}
                  </ErrorBoundary>
                </Content>
                {!noFooterRoutes.includes(pathname) ? (
                  <div
                    style={{
                      background: '#f0f2f5',
                      overflow: 'hidden',
                      marginLeft: '10px',
                    }}
                  >
                    <div
                      style={{
                        height: 0,
                      }}
                    />
                    <GlobalFooter className={styles.myglobalFooter} />
                  </div>
                ) : null}
              </div>
              {/* </Scrollbars> */}
            </div>
          </Layout>
        </Layout>
      </Layout>
    );
  };

  render() {
    const { menuLoading, getUserInfoLoading } = this.props;
    const { initMenuLoading } = this.state;
    return (
      <DocumentTitle title={this.getPageTitle()}>
        <Spin
          spinning={
            Boolean(menuLoading) ||
            Boolean(initMenuLoading) ||
            Boolean(getUserInfoLoading)
          }
          style={{
            minHeight: 'calc(100vh)',
          }}
        >
          <ContainerQuery query={SCREENMAP}>
            {(params) => (
              <div className={classNames(params)}>
                {this.renderBasicLayout()}
              </div>
            )}
          </ContainerQuery>
        </Spin>
      </DocumentTitle>
    );
  }
}

export default withRouter(
  connect(({ user, global, loading, pageManage, oem, psNotification }) => ({
    user,
    currentUser: user.currentUser,
    notifyCount: user.notifyCount,
    platformInfo: user.timestats,
    collapsed: global.collapsed,
    fetchingNotices:
      loading.effects['global/fetchNotices'] ||
      loading.effects['global/clearNotices'],
    notices: global.notices,
    currentMenuData: user.currentMenuData, // menu.js替代品
    menuData: user.menuData,
    filterVersionRoutesArr: user.filterVersionRoutesArr,
    routerDatas: user.routerDatas, // router.js替代品
    reqAdminData: user.reqAdminData,
    menuLoading: loading.effects['oem/setOemInfo'],
    getUserInfoLoading: loading.effects['user/getUserInfo'],
    pageManage,
    oem,
    psNotification,
  }))(BasicLayout),
);
