import React, { PureComponent } from 'react';
import { connect } from 'dva';
import { Icon as LegacyIcon } from '@ant-design/compatible';
import CS from 'classnames';

import {
  Layout,
  Menu,
  Spin,
  Dropdown,
  Avatar,
  Divider,
  // Popover,
  Modal,
  Space,
} from 'antd';
import Bind from 'lodash-decorators/bind';
import Debounce from 'lodash-decorators/debounce';
// eslint-disable-next-line import/no-extraneous-dependencies
import { Link } from 'umi';
import PropTypes from 'prop-types';
import { IconFont, Ellipsis } from '@/components';
import { RegulateURL } from '@/utils';
// import { moduleMap } from '@/utils';
import CountTime from '@/components/CountTime';

import NoticeIcon from './NoticeIcon';

import styles from './index.less';

const { Header } = Layout;
const { SubMenu, Item: MenuItem } = Menu;
const isPreviewEdition = process.env.BUILD_MODE === 'preview';

@connect(({ user, pageManage }) => ({
  user,
  pageManage,
}))
export default class GlobalHeader extends PureComponent {
  // eslint-disable-next-line react/sort-comp
  constructor(props) {
    super(props);
    this.state = {
      shownMenuNum: 0,
    };
  }

  // eslint-disable-next-line react/static-property-placement
  static contextTypes = {
    platformInfo: PropTypes.object,
  };

  componentDidMount() {
    // this.getShowMenuNum();
    window.addEventListener('resize', this.resize);
  }

  componentWillUnmount() {
    this.triggerResizeEvent.cancel();
    window.removeEventListener('resize', this.resize);
  }

  /**
   * 动态计算showMenuNum
   * 240 = 160(左侧树) + 80(更多)
   * 224 = 右侧用户相关信息大致宽度
   * 112 = 四个中文字符导航item的width
   */

  getShowMenuNum = (widthArr = this.props.user.mockNavMenuWidth) => {
    const headerRight = document.querySelector('.headerRight');

    // 中间留给显示菜单的位置
    let spaceWidth = Math.floor(window.innerWidth - 160 - 224);
    let shownMenuNum = 0;
    if (headerRight) {
      const curRightWidth = headerRight.clientWidth;
      spaceWidth = Math.floor(window.innerWidth - 160 - curRightWidth);
      let curWidth = 0;
      for (let index = 0; index < widthArr.length; index++) {
        curWidth += widthArr[index];
        if (curWidth < spaceWidth) {
          if (index === widthArr.length - 1) {
            shownMenuNum = widthArr.length;
          }
        } else {
          shownMenuNum = index - 1;
          break;
        }
      }
    }

    // 兼容 预览页面下的导航展示
    const { hashParams } = new RegulateURL();
    const domHeader = document.querySelector('.ant-layout-header');
    if (
      domHeader &&
      hashParams.hasOwnProperty('isPreview') &&
      hashParams.isPreview === 'true'
    ) {
      if (headerRight) {
        const curRightWidth = headerRight.clientWidth;
        spaceWidth = Math.floor(1920 - 160 - curRightWidth);

        let curWidth = 0;
        for (let index = 0; index < widthArr.length; index++) {
          curWidth += widthArr[index];
          // console.log('curWidth', curWidth);
          if (curWidth < spaceWidth) {
            if (index === widthArr.length - 1) {
              shownMenuNum = widthArr.length;
            }
          } else {
            shownMenuNum = index - 1;
            break;
          }
        }
      }
    }
    this.setState({
      shownMenuNum,
    });
    return shownMenuNum;
  };

  getSelectedTopMenuKey = (pathname) => {
    return pathname.split('/')[1];
  };

  getFormatTopMenuData = (menuData) => {
    return menuData.map((item) => {
      const { path, children } = item;
      const obj =
        children &&
        children.find((child) => {
          return (
            child.path.startsWith('process') ||
            (isPreviewEdition
              ? child.isPriview === true || child.isPriview === undefined
              : child.isPriview === false || child.isPriview === undefined)
          );
        });
      const formatPath = children && obj ? obj.path : path;
      const key = children ? path : path.split('/')[0];
      return {
        ...item,
        key,
        path: this.conversionPath(formatPath),
      };
    });
  };

  getTopMenuItems = (menuData, shownMenuNum = 5) => {
    const hiddenMenuNum = menuData.length - shownMenuNum;
    const topMenuData = this.getFormatTopMenuData(menuData);

    const shownMenuItem = topMenuData.slice(0, shownMenuNum).map((menu) => {
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      const { name, key, path, icon } = menu;
      return (
        <MenuItem key={key}>
          <Link to={path} title={name.length > 8 ? name : undefined}>
            {/* 去除导航栏图标 */}
            {/* {icon ? <IconFont type={icon} /> : null} */}
            {name.length > 8 ? `${name.slice(0, 8)}...` : `${name}`}
          </Link>
        </MenuItem>
      );
    });
    const hiddenMenuItem =
      hiddenMenuNum > 0 ? (
        <SubMenu
          popupClassName={styles.moreSubMenu}
          title="更多"
          key="hiddenMenu"
        >
          {topMenuData.slice(shownMenuNum).map((item) => {
            const { name, path, key } = item;
            return (
              <MenuItem key={key + name}>
                <Link to={path} title={name.length > 8 ? name : undefined}>
                  {name.length > 8 ? `${name.slice(0, 8)}...` : `${name}`}
                </Link>
              </MenuItem>
            );
          })}
        </SubMenu>
      ) : null;
    return hiddenMenuItem
      ? shownMenuItem.concat(hiddenMenuItem)
      : shownMenuItem;
  };

  conversionPath = (path) => {
    if (path && path.indexOf('http') === 0) {
      return path;
    } else {
      return `/${path || ''}`.replace(/\/+/g, '/');
    }
  };

  toggle = () => {
    const { collapsed, onCollapse } = this.props;
    onCollapse(!collapsed);
    this.triggerResizeEvent();
  };
  // eslint-disable-next-line
  @Bind()
  @Debounce(300)
  resize() {
    const shownMenuNum = this.getShowMenuNum();
    this.setState({
      shownMenuNum,
    });
  }

  @Debounce(600)
  // eslint-disable-next-line class-methods-use-this
  triggerResizeEvent() {
    // eslint-disable-line
    const event = document.createEvent('HTMLEvents');
    event.initEvent('resize', true, false);
    window.dispatchEvent(event);
  }

  handleChangeProject = ({ key }) => {
    const {
      pageManage: { isPreview, releasedPageInfo },
    } = this.props;

    if (isPreview) {
      Modal.confirm({
        title: '发布取消',
        content: <p>页面还未发布，离开将不保存页面，是否离开？</p>,
        onOk: () => {
          this.props
            .dispatch({
              type: 'pageManage/recovery',
              payload: releasedPageInfo,
            })
            .then(({ code }) => {
              if (code === 200) {
                this.props
                  .dispatch({
                    type: 'pageManage/clearMenu',
                  })
                  .then(({ code: clearMenuCodee }) => {
                    if (clearMenuCodee === 200) {
                      this.props.dispatch({
                        type: 'pageManage/updateState',
                        payload: {
                          isPreview: false,
                        },
                      });
                      this.props.handleChangeProject(key);
                    }
                  });
              }
            });
        },
        onCancel: () => {},
        cancelText: '取消',
        okText: '离开',
      });
    } else {
      this.props.handleChangeProject(key);
    }
  };

  getProjectOption = () => {
    const {
      user: { projectInfo },
    } = this.props;
    return (
      <Menu className={styles.menu} onClick={this.handleChangeProject}>
        {projectInfo.map((item) => (
          <Menu.Item key={item.projectid} disabled={item.isdelete}>
            <Ellipsis tooltip lines={1}>
              {item.projectname}
            </Ellipsis>
          </Menu.Item>
        ))}
      </Menu>
    );
  };

  getCurrentSystemInfo = () => {
    this.props.dispatch({
      type: 'user/getOverviewInfo',
    });
  };

  render() {
    const {
      user: { projectInfo, projectName, mockNavMenuWidth },
      currentUser = {},
      notifyCount,
      collapsed,
      fetchingNotices,
      isMobile,
      logo,
      location: { pathname, search },
      onNoticeVisibleChange,
      onMenuClick,
      onNoticeClear,
      onRedirect,
      menuData,
      notices,
    } = this.props;
    const {
      platformInfo: { nowtime, runtime },
    } = this.context;
    const { shownMenuNum } = this.state;
    const oemInfo = localStorage.getItem('oemInfo') || '{}';
    const { logofile, isoem, ip } = JSON.parse(oemInfo);
    const hostIp = process.env.NODE_ENV !== 'development' ? location.host : ip;
    // 如果OEM功能为'true'，并且有图片地址，使用OEM配置里面的图片，否则使用默认图片
    // console.log(`//${hostIp}/${logofile}`);
    const logoImg =
      isoem === 'true' && ip && logofile ? (
        <img
          alt="logo"
          src={`//${hostIp}/${logofile}`}
          style={{ width: 120, height: 30, position: 'absolute', top: 9 }}
        />
      ) : (
        <img src={logo} alt="" className={styles.logo_svg} />
      );

    const menu = (
      <Menu className={styles.menu} selectedKeys={[]} onClick={onMenuClick}>
        <Menu.Item key="profile">
          <IconFont type="icon-idcard" />
          基本信息
        </Menu.Item>
        {currentUser?.userid === 'admin_user' && (
          <Menu.Item key="log">
            <IconFont type="icon-log-operate" />
            操作记录
          </Menu.Item>
        )}
        <Menu.Divider />
        <Menu.Item key="logout">
          <IconFont type="icon-logout" />
          退出登录
        </Menu.Item>
      </Menu>
    );

    const isPreviewPage = search.includes('isPreview=true');

    const curMenuNum =
      shownMenuNum === 0 ? this.getShowMenuNum(mockNavMenuWidth) : shownMenuNum;
    const curUsername = isPreviewPage ? '预览' : currentUser.username;
    return (
      <Header className={styles.header}>
        {isMobile ? (
          [
            <Link to="/" className={styles.logo} key="logo">
              <img src={logoImg} alt="logo" width="32" />
            </Link>,
            <Divider type="vertical" key="line" />,
            <LegacyIcon
              className={styles.trigger}
              type={collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={this.toggle}
            />,
          ]
        ) : (
          <div className={styles.logo} key="logo">
            <Link to="/">{logoImg}</Link>
          </div>
        )}
        <Menu
          className={styles.heaerTopMenu}
          mode="horizontal"
          theme="dark"
          selectedKeys={[this.getSelectedTopMenuKey(pathname)]}
        >
          {this.getTopMenuItems(menuData, curMenuNum)}
        </Menu>
        <div className={CS(styles.right, 'headerRight')}>
          {projectInfo.length > 0 && (
            <Dropdown
              overlay={this.getProjectOption()}
              placement="bottomRight"
              overlayClassName={styles.dropdownProject}
              trigger={['click']}
            >
              <Space className={styles.projectName}>
                <Ellipsis tooltip lines={1} style={{ maxWidth: 160 }}>
                  {projectName}
                </Ellipsis>
                <IconFont type="icon-down" />
              </Space>
            </Dropdown>
          )}
          <Dropdown
            onMouseEnter={this.getCurrentSystemInfo}
            trigger={['click']}
            placement="bottomRight"
            overlay={
              <div className={styles.menuLike}>
                <div>平台系统时间：</div>
                <div>
                  <CountTime base={nowtime} />
                </div>
                <div>
                  已连续运行：
                  {runtime}天
                </div>
              </div>
            }
          >
            <span className={styles.action}>
              <IconFont type="icon-clock-circle" />
            </span>
          </Dropdown>
          <NoticeIcon
            className={styles.action}
            count={notifyCount}
            onItemClick={onRedirect}
            onClear={onNoticeClear}
            onPopupVisibleChange={onNoticeVisibleChange}
            fetchingNotices={fetchingNotices}
            notices={notices}
          />
          {currentUser.username ? (
            <Dropdown overlay={menu} trigger={['click']}>
              <span className={`${styles.action} ${styles.account}`}>
                <Avatar
                  size="small"
                  className={styles.avatar}
                  src={currentUser.avatar_base64}
                />
                <span style={{ display: 'inline-flex' }}>
                  <Ellipsis tooltip lines={1} style={{ maxWidth: 160 }}>
                    <span className={styles.name}>{curUsername}</span>
                  </Ellipsis>
                </span>
              </span>
            </Dropdown>
          ) : (
            <Spin size="small" style={{ marginLeft: 8 }} />
          )}
        </div>
      </Header>
    );
  }
}
