import React from 'react';
import { Collapse } from 'antd';
import errPage from '../assets/errPage.png';
import PageHeaderLayout from './PageHeaderLayout';
import styles from './errBoundary.less';

const { Panel } = Collapse;

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null, errorInfo: null };
  }

  componentDidCatch(error, errorInfo) {
    // console.log('[myErrorBoundary]', error, errorInfo);
    // Catch errors in any components below and re-render with error message
    this.setState({
      error,
      errorInfo,
    });
    // You can also log error messages to an error reporting service here
  }

  render() {
    if (this.state.errorInfo) {
      // Error path
      return (
        <PageHeaderLayout>
          <div className={styles.errBoundary}>
            <div>
              <div className={styles.errText1}>网页崩溃了</div>
              <div className={styles.errText2}>
                请<span onClick={() => window.location.reload()}>刷新</span>
                重试
              </div>
              <img src={errPage} alt="暂无图片" />
            </div>
            <Collapse bordered={false}>
              <Panel
                header={this.state.error && this.state.error.toString()}
                key="1"
                style={{ whiteSpace: 'pre-wrap' }}
              >
                {this.state.errorInfo.componentStack.trimLeft()}
              </Panel>
            </Collapse>
          </div>
        </PageHeaderLayout>
      );
    }
    // Normally, just render children
    return this.props.children;
  }
}

export default ErrorBoundary;
