import { originalMenuData } from '@/config/menu';
import { isUrl } from '@/utils';

export const isSiderRoutes = (noSiderRoutes, pathname) => {
  return (
    pathname.split('/').length > 2 &&
    noSiderRoutes.every((item) => !pathname.includes(item))
  );
};

export const formatter = (
  data,
  parentPath = '',
  parentAuthority = ['admin', 'user'],
) => {
  return data.map((item) => {
    let { path } = item;
    if (!isUrl(path)) {
      path = parentPath + item.path;
    }
    const result = {
      ...item,
      path,
      authority: item.authority || parentAuthority,
      redirect: item.redirect || null, // 添加重定向
    };
    if (item.children) {
      result.children = formatter(
        item.children,
        `${parentPath}${item.path}/`,
        item.authority || parentAuthority,
      );
    }
    return result;
  });
};

export const formatedOriginMenuData = formatter(originalMenuData); // 需要使用到到menu数据
